#gridContainer {
  border-top: solid;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  margin: 10px;
  padding-top: 20px;
  width: 95%;
}

#homeArticleContainer {
  border: solid;
  border-radius: 10px;
  border-width: 1px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin: 10px;
  margin-bottom: 20px;
  padding: 10px;
}

#otherBlogs {
  text-align: center;
  width: 100%;
}

.gridBox {
  border: solid;
  border-width: 2px;
  border-radius: 10px;
  font-size: 18px;
  justify-content: center;
  text-align: center;
  width: 100px;
}

.gridDate {
  font-size: 14px;
}

.gridImage {
  width: 100%;
}

.homeDate {
  font-family: 'Orbitron', sans-serif;
  font-size: 16px;
  font-weight: 500;
  margin-right: 15px;
  text-align: end;
}

.homeImage {
  align-self: center;
  width: 200px;
}

.homeText {
  font-size: 20px;
  padding: 15px;
}

.homeTitle {
  font-family: 'Bebas Neue', cursive;
  font-size: 34px;
  text-align: center;
  text-decoration: underline;

}
body {
  margin: 5;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* width */
::-webkit-scrollbar {

  width: 12px;

}

/* Track */
/* ::-webkit-scrollbar-track {
  background: #0ab67d;

} */

/* Handle */
::-webkit-scrollbar-thumb {
  background: #00ff55;
  border-radius: 10px;

}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: red;
}